.roadmap {
  position: relative;
  max-width: 100%;
  margin: 0 auto;
}

.roadmap::after {
  content: '';
  position: absolute;
  width: 6px;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}

.roadmap-container {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
}

.roadmap-container::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 25px;
  right: -12.5px;
  background-color: white;
  border: 4px solid #FF9F55;
  top: 45px;
  border-radius: 50%;
  z-index: 1;
}

#roadmap-start{
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
  height:125px;
}

#roadmap-start::after{
  content: '';
  position: absolute;
  width: 75px;
  height: 75px;
  right: -37.5px;
  background-color: white;
  border: 4px solid #FF9F55;
  top: 0px;
  border-radius: 50%;
  z-index: 1;
  background-image: url(/bogwebsite/static/media/start.81a938f5.png);
  background-size: cover;
  background-repeat: no-repeat;
}

#roadmap-end{
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
  height:125px;
}

#roadmap-end::after{
  content: '';
  position: absolute;
  width: 75px;
  height: 75px;
  right: -37.5px;
  background-color: white;
  border: 4px solid #FF9F55;
  bottom: 0px;
  border-radius: 50%;
  z-index: 1;
  background-image: url(/bogwebsite/static/media/end.d27ebd78.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.roadmap-left{
  left: 0;
}

.roadmap-right {
  left: 50%;
}

.roadmap-right::after {
  left: -12.5px;
}

.roadmap-content {
